import React from 'react';
import PropTypes from 'prop-types';
import bridge from '@vkontakte/vk-bridge';

import { Panel, PanelHeader,PanelHeaderBack,SimpleCell, Snackbar, FixedLayout,CardGrid,Card, RichCell,Header,Separator, Title,Subhead, MiniInfoCell, Button, Group, Cell, Div, Avatar } from '@vkontakte/vkui';
import { Icon20VideoOutline, Icon20RecentOutline,Icon24Done,Icon24Copy,Icon24Link,Icon24VideoCircleOutline,Icon24Favorite,Icon20FavoriteCircleFillYellow,Icon20GlobeOutline, Icon20VideocamOutline,Icon20CalendarOutline } from '@vkontakte/icons';

const Persik = ({ id, go,setPopout, TitleFilmByid,SubnavigationButton,SubnavigationBar, activeWindow, descriptionByid, FilmsByid, ButtonNext, fetchedUser }) => (
	<Panel id={id}>
		<PanelHeader left={<PanelHeaderBack data-to="FavoritesPanel" onClick={go} />}>Закладки</PanelHeader>
		{FilmsByid &&
        <Div style={{ float: "center", display: "inline-block"}} >	
        <img style={{ width : "40%", float: "left", display: "inline-block", borderRadius: 10}} src={FilmsByid.data.posterUrl} />		
        <Title className="Text" style={{ float: "left", width : "50%", marginLeft: 16}} level="2" weight="bold">{TitleFilmByid}</Title>
        <MiniInfoCell
		  style={{ float: "left", width : "45%", whiteSpace: "nowrap", overflow: "hidden"}}
          before={<Icon20GlobeOutline fill="#fc853a"/>}
          textWrap="full"
          textLevel="primary"
        >
		{FilmsByid.data.countries[0].country}
        </MiniInfoCell>
		<MiniInfoCell
		  style={{ float: "left", width : "50%"}}
          before={<Icon20CalendarOutline fill="#fc853a"/>}
          textWrap="full"
          textLevel="primary"
        >
		Год: {FilmsByid.data.year}
        </MiniInfoCell>
		<MiniInfoCell
		  style={{ float: "left", width : "50%"}}
          before={<Icon20RecentOutline fill="#fc853a" />}
          textWrap="full"
          textLevel="primary"
        >
		Длительность: {FilmsByid.data.filmLength}
        </MiniInfoCell>
		<MiniInfoCell
		  style={{ float: "left", width : "50%"}}
          before={<Icon20VideoOutline fill="#fc853a" />}
          textWrap="full"
          textLevel="primary"
        >
		Жанр: {FilmsByid.data.genres[0].genre}
        </MiniInfoCell>
		<MiniInfoCell
		  style={{ float: "left", width : "50%"}}
          before={<Icon20FavoriteCircleFillYellow />}
          textWrap="full"
          textLevel="primary"
        >
		Рейтинг: {FilmsByid.rating.rating}/10
        </MiniInfoCell>
        </Div>}
		{FilmsByid && 
		<div>
		<Separator style={{ marginTop: 8}} />
		<SimpleCell before={<Icon24Copy fill="#fc853a"/>}
		onClick={() => {
			bridge.send("VKWebAppCopyText", {"text": FilmsByid.data.nameRu});
			setPopout(
			<Snackbar before={<Icon24Copy fill="#fc853a"/>} onClose={() => setPopout(null)} duration={1000} >
			Скопировано
			</Snackbar>
			)
			bridge.send("VKWebAppTapticNotificationOccurred", {"type": "success"});
		}}
		>Копировать название</SimpleCell>
		<SimpleCell id={FilmsByid.data.idfilm} style={{marginTop: -10}} before={<Icon24Favorite fill="#fc853a"/>}
		onClick={() => {
			async function DeleteFilm(){
			const startParams = new URLSearchParams(window.location.search)
            const allparams = btoa(window.location.search)
			const result = await fetch('https://simpletraffic.xyz/vkapi/kino.php?action=delete&key=' + allparams + "&id=" + FilmsByid.data.filmId);
			const datas = await result.json()
			setPopout(
			<Snackbar before={<Icon24Done fill="#fc853a"/>} onClose={() => setPopout(null)} duration={1000} >
				{datas.text}
			</Snackbar>
			)
			bridge.send("VKWebAppTapticNotificationOccurred", {"type": "error"});
			}DeleteFilm()
		}}
		>Удалить из закладок</SimpleCell>
		<Separator style={{ marginTop: 0}} />
		</div>}
		{FilmsByid && 
		<Group header={<Header mode="secondary">Описание "{FilmsByid.data.nameRu}"</Header>}>
		<Div style={{marginBottom: "25%"}}>
		<Subhead weight="regular">{descriptionByid}<br/><br/></Subhead>
		</Div>
		</Group>}
	</Panel>
);
export default Persik;
